import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form, message, Input, Button } from 'antd';
import { TableDataEditRow, CartHeadAction } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';
import { getAccountsReceivable, updateAdmissionFee } from '../../../services/admissionFee';
import { moneyToString } from '../../../utils/formatUtil';

class AdmissionFee extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  columns = [
    {
      title: 'Ngnh o to',
      dataIndex: 'nganhdaotao',
      key: 'nganhdaotao',
    },
    {
      title: 'Khon thu nhp hc',
      dataIndex: 'khoanthunhaphoc',
      key: 'khoanthunhaphoc',
    },
    {
      title: 'S tin',
      dataIndex: 'sotien',
      key: 'sotien',
      editable: true,
      render: text => moneyToString(text),
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('sotien', {
            initialValue: record.sotien,
          })(<Input />)}
        </Form.Item>
      ),
    },
  ];

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleCopyToNextYear = () => {
    message.success('Sao chp sang k sau thnh cng');
  }

  handleSaveRowData = async rowData => {
    try {
      await updateAdmissionFee(rowData);
      return rowData;
    } catch (e) {
      return null;
    }
  };

  render() {
    return (
      <Card>
        <CartHeadAction>
          <Button type='primary' onClick={this.handleCopyToNextYear}>Sao chp sang k sau</Button>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <SearchBar />
          </Col>
          <Col span={24}>
            <TableDataEditRow
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getAccountsReceivable}
              columns={this.columns}
              handleSaveRowData={this.handleSaveRowData}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AdmissionFee />
  </Page>
);



